import layout from '@/views/layout'

export default [
  {
    path: '/contact',
    component: layout,
    meta: {
      title: '联系我们'
    },
    children: [
      {
        path: '',
        name: 'Contact',
        component () {
          return import(/* webpackChunkName: "Contact" */ '@/views/contact')
        },
        meta: {
          title: '联系我们'
        }
      }
    ]
  },
]
