import layout from '@/views/layout'

export default [
  {
    path: '/union',
    component: layout,
    redirect: '/union/introduce',
    meta: {
      title: '风能专委会'
    },
    children: [
      {
        path: 'introduce',
        name: 'Introduce',
        component () {
          return import(/* webpackChunkName: "Introduce" */ '@/views/union/introduce.vue')
        },
        meta: {
          title: '协会简介'
        }
      },
      {
        path: 'structure',
        name: 'Structure',
        component () {
          return import(/* webpackChunkName: "Structure" */ '@/views/union/structure.vue')
        },
        meta: {
          title: '组织结构'
        }
      },
      {
        path: 'history',
        name: 'History',
        component () {
          return import(/* webpackChunkName: "History" */ '@/views/union/history.vue')
        },
        meta: {
          title: '历史进程'
        }
      },
    ]
  }
]
