import layout from '@/views/layout'

export default [
  {
    path: '/news',
    component: layout,
    redirect: '/news/lastest',
    meta: {
      title: '新闻中心'
    },
    children: [
      {
        path: 'lastest',
        name: 'Lastest',
        component () {
          return import(/* webpackChunkName: "Lastest" */ '@/views/news/lastest.vue')
        },
        meta: {
          title: '最新新闻'
        }
      },
      {
        path: 'theme',
        name: 'Theme',
        component () {
          return import(/* webpackChunkName: "Theme" */ '@/views/news/theme.vue')
        },
        meta: {
          title: '专题新闻'
        }
      },
      {
        path: 'video',
        name: 'Video',
        component () {
          return import(/* webpackChunkName: "Video" */ '@/views/news/video.vue')
        },
        meta: {
          title: '视频中心'
        }
      },
      {
        path: 'detail/:id',
        name: 'NewsDetail',
        component () {
          return import('@/views/news/detail')
        },
        meta: {
          title: '新闻详情页'
        }
      }
    ]
  },
]
