<template>
  <svg class="svg-icon" aria-hidden="true">
    <use :xlink:href="iconName"></use>
  </svg>
</template>

<script>
export default {
  name: 'BaseIcon',
  data () {
    return {}
  },
  props: {
    iconClass: {
      type: String,
      required: true
    }
  },
  computed: {
    iconName: function () {
      return `#icon-${this.iconClass}`
    }
  }
}
</script>

<style lang="scss" scoped>
  // .icon
  .svg-icon {
    width: 200px;
    height: 190px;
  }
</style>
