import layout from '@/views/layout'

export default [
  {
    path: '/member',
    component: layout,
    redirect: '/member/company',
    meta: {
      title: '会员服务'
    },
    children: [
      {
        path: 'company',
        name: 'Company',
        component () {
          return import(/* webpackChunkName: "Company" */ '@/views/member/company.vue')
        },
        meta: {
          title: '企业会员'
        }
      },
      {
        path: 'join',
        name: 'Join',
        component () {
          return import(/* webpackChunkName: "Join" */ '@/views/member/join.vue')
        },
        meta: {
          title: '企业会员'
        }
      },
      {
        path: 'company-news',
        name: 'CompanyNews',
        component () {
          return import(/* webpackChunkName: "CompanyNews" */ '@/views/member/company-news.vue')
        },
        meta: {
          title: '企业会员'
        }
      },
    ]
  }
]
