const files = require.context('.', false, /\.js$/)
let allRoutes = []
let navRoutes = []

// item route 其实是匹配到的文件的相对路径
files.keys().forEach((route) => {
  if (route === './index.js') return
  const pagePath = files(route).default
  allRoutes = allRoutes.concat(pagePath)

  const n = route.search(/\.nav\.js$/)
  if (n >= 0) {
    navRoutes = navRoutes.concat(pagePath)
  }
})

export { navRoutes }

export default allRoutes
