import layout from '@/views/layout'

export default [
  {
    path: '/magazine',
    component: layout,
    redirect: '/magazine/about',
    meta: {
      title: '风能杂志'
    },
    children: [
      {
        path: 'about',
        name: 'About',
        component () {
          return import(/* webpackChunkName: "About" */ '@/views/magazine/about.vue')
        },
        meta: {
          title: '关于杂志'
        }
      },
      {
        path: 'download',
        name: 'Download',
        component () {
          return import(/* webpackChunkName: "Download" */ '@/views/magazine/download.vue')
        },
        meta: {
          title: '下载杂志'
        }
      },
    ]
  },
]
