<script>
import { list } from './index-magazine-list.js'
import { lastest, qiye } from './index-news.js'
import CweaFooter from '@/views/layout/components/cwea-footer.vue'
import IndexNav from './index-nav.vue'
import './index-fullpage.scss'

export default {
  name: 'FullPageIndex',
  data () {
    return {
      showHeader: false,
      list,
      lastest,
      qiye,
      magazineFocus: 1,
      // 移动的距离，初始为 0
      leftEnd: 0,
      options: {
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        afterLoad: this.afterLoad,
        onLeave: this.onLeave,
        navigation: false,
        navigationPosition: 'right',
        anchors: ['page1', 'page2', 'page3'],
        /*
         *  sectionsColor: [
         *  '#41b883',
         *  '#ff5f45',
         *  '#0798ec',
         *  '#fec401',
         *  '#1bcee6',
         *  '#ee1a59',
         *  '#2c3e4f',
         *  '#ba5be9',
         *  '#b4b8ab'
         *  ],
         */
        loopBottom: true,
        // navigationTooltips导航小圆点的提示，注意按顺序设置
        navigationTooltips: ['page1', 'page2', 'page3', 'page4', 'page5'],
        // showActiveTooltip是否显示当前页面的tooltip信息，默认为false
        showActiveTooltip: true,
        // slidesNavigation 是否显示横向幻灯片的导航，默认为false
        slidesNavigation: true,
        // slidesNavPosition横向导航的位置，默认为bottom，可以设置为top或bottom
        slidesNavPositon: 'top',
        // scrollOverflow内容超过满屏后是否显示滚动条，默认为false,如果为true则会显示滚动条，若需滚动查看内容还需要jquery.slimscroll插件的配合

        // sectionSelector:section选择器。默认为.section

        // slideSelector:slide选择器，默认为.slide
        slideSelector: '.slide',

        controlArrows: true,
        scrollBar: true,
      }
    }
  },
  components: {
    CweaFooter,
    IndexNav
  },
  computed: {
    magazineLength () {
      return this.list.length
    }
  },
  methods: {
    afterLoad: function (origin, destination, direction) {
      if (destination.anchor === 'page1') {
        this.showHeader = false
      } else {
        this.showHeader = true
      }
    },
    onLeave: function (origin, destination, direction) {
      // console.log('离开过程执行，leave', origin.index)
      this.showHeader = false
    },
    buildFullPage () {
      // :skip-init="true" 结合这个属性使用
      this.$refs.fullpage.init()
    },
    destroyFullPage () {
      this.$refs.fullpage.destroy()
    },
    testClick (str) {
      window.alert(str)
    },
    warn (item, index) {
      console.log(index, item)
      this.magazineFocus = index
    },
    move (num) {
      // +120 是向右，-120 是向左，num 本身为 1 或 -1，1 是向右，-1 是向左

      // 左边界 (length - 4)*120*(-1) ~ (length - 4)*120
      if (num > 0 && this.leftEnd < 0) {
        console.log('个数', this.leftEnd, num)
        this.leftEnd = this.leftEnd + 120
      } else if (num < 0 && this.leftEnd > (4 - this.magazineLength) * 120) {
        this.leftEnd = this.leftEnd - 120
      } else {
        this.leftEnd = 0
      }
    }
  },
  activated () {
    // this.buildFullPage()
    console.log('缓存的组件被激活了')
  },
  deactivated () {
    // 多试几次，slide 下边的点数量出 bug 了

    // this.destroyFullPage()
    console.log('缓存的组件失去激活，休眠')
  },
}
</script>

