const lastest = {
  url: 'api/news/lastest',
  type: 'get',
  data: {
    'list|2-3': [
      {
        id: '@id',
        url: 'detail/' + '@id',
        date: "@date('yyyy-MM-dd')",
        title: '@ctitle(8, 13)',
        'paragraphs|4-10': [
          '@cparagraph'
        ]
      }
    ]
  }
}

const detail = {
  url: /api\/news\/\d*/,
  type: 'get',
  data: {
    id: '@id',
    url: 'detail/' + '@id',
    date: "@date('yyyy-MM-dd')",
    title: '@ctitle(8, 13)',
    'paragraphs|4-10': [
      '@cparagraph'
    ]
  }
}

export default [
  lastest,
  detail
]
