export default [
  {
    path: '/page',
    meta: {
      title: '测试页面',
    },
    component () {
      return import(/* webpackChunkName: "PageTest" */ '@/views/page')
    },
    // 结合子路由，在这里设置无效 redirect: '/page/index',
    children: [
      // path: '' 会访问谁
      {
        path: '',
        name: 'PageDefault',
        // 这里才会有作用，实际用处 redirect: 'index',
        component () {
          return import('@/views/page/home.vue')
        }
      },
      // path: 'index' 会访问谁
      {
        path: 'index',
        name: 'PageIndex',
        component () {
          return import('@/views/page/home-new.vue')
        }
      },
      {
        path: 'son',
        // name: 'PageSon',
        component () {
          return import('@/views/page/son.vue')
        },

        children: [
          {
            path: '',
            name: 'SonDefault',
            redirect: 'demo2',
            component () {
              return import('@/views/page/son-page/index.vue')
            },
          },
          {
            path: 'demo1',
            name: 'SonDemo1',
            component: () => import('@/views/page/son-page/son-demo1.vue')
          },
          {
            path: 'demo2',
            name: 'SonDemo2',
            component: () => import('@/views/page/son-page/son-demo2.vue')
          },
        ]


      },
    ]
  }
]
