import layout from '@/views/layout'

export default [
  {
    path: '/industry',
    component: layout,
    redirect: '/industry/project',
    meta: {
      title: '产业首页'
    },
    children: [
      {
        path: 'project',
        name: 'Project',
        component () {
          return import(/* webpackChunkName: "Project" */ '@/views/industry/project.vue')
        },
        meta: {
          title: '研究项目'
        }
      },
      {
        path: 'publish',
        name: 'Publish',
        component () {
          return import(/* webpackChunkName: "Publish" */ '@/views/industry/publish.vue')
        },
        meta: {
          title: '出版物'
        }
      },
      {
        path: 'data',
        name: 'Data',
        component () {
          return import(/* webpackChunkName: "Data" */ '@/views/industry/data.vue')
        },
        meta: {
          title: '风电产业数据'
        }
      },
    ]
  },
]
