export function navCheck (arr, path) {
  arr.forEach((item, index) => {
    if (item.path === path) {
      return index
    }
  })
}

export function headerNav (send) {
  var res = {}
  navData.forEach((item, index) => {
    if (item.path === send) {
      var { title, secondTitle, bgImg } = item
      res = { title, secondTitle, bgImg }
    } else if (item.children) {
      item.children.forEach((m) => {
        if (m.path === send) {
          var { title, secondTitle, bgImg } = item
          res = { title, secondTitle, bgImg }
        }
      })
    }
  })
  return res
}

// 循环一直在执行，不好...

// 该停的地方，应该停下来... 再想吧。 重复计算、没有意义，纯属浪费
export function secondNav (send, res = { path: '/', title: 'Home' }) {
  // 这个循环还是总会执行一遍，此时 for 和 forEach 就没区别了，不是最节省逻辑的方法
  for (var i = 0; i < navData.length; i++) {
    // 如果项目有 children ； children 中是否有传入项 ，有则 改一下 res
    if (navData[i].children) {
      navData[i].children.some((m, n) => {
        if (m.path === send) {
          var { path, title, children } = navData[i]
          res = { path, title, children, fatherIndex: i, sonIndex: n }
          // return res
        }
      })
    } else if (navData[i].path === send) {
      // item 项没有 children , 本身是否 === send , 如果等于 改变 res
      var { path, title } = navData[i]
      res = { path, title }
      // return res
    }
  }
  return res
}
